@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:900i&display=swap);
:root {
  --border-width: 0.1875em;
}

* {
  box-sizing: border-box;
}
body {
  background-image: url(/static/media/bgr.52a2f4c9.jpg);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Source Sans Pro', sans-serif;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.icon-back{
  color: #bfab97;
  font-size: 1.5em;
  position: absolute;
  left: 250px;
  top:250px;
}
.icon-next{
  color: #bfab97;
  font-size: 1.5em;
  position: absolute;
  right:250px;
  top:250px;
}
.button {
  position: absolute;
  bottom: 0;
  right: 500px;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.25rem;
  line-height: 1;
  -moz-appearance: none;
  -webkit-appearance: none;
  vertical-align: middle;
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  font-weight: 900;
  font-style: italic;
  margin: 0 auto;
  padding: 0.75em 1.5em;
  background: linear-gradient(rgba(0, 0, 50, 0.3), rgba(0, 0, 50, 0.3)), linear-gradient(-60deg, #fc00ff, #00dbde);
  background-clip: padding-box, border-box;
  background-size: 100% 100%, calc(100% + 0.1875em + 0.1875em) calc(100% + 0.1875em + 0.1875em);
  background-size: 100% 100%, calc(100% + var(--border-width) + var(--border-width)) calc(100% + var(--border-width) + var(--border-width));
  background-position: 0 0, calc(0.1875em * -1) calc(0.1875em * -1);
  background-position: 0 0, calc(var(--border-width) * -1) calc(var(--border-width) * -1);
  border-radius: 0.875em;
  border: 0.1875em solid transparent;
  border: var(--border-width) solid transparent;
  box-shadow: 0 0.125em 0.25em #003b, 0 0 2em #00dbdeaa;
  cursor: pointer;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
}
.button:hover {
  box-shadow: 0 0.25em 0.75em #003b, 0 0.125em 2em #00dbdeff;
  transform: translateY(-0.125em);
}
.pause{
  left: 500px;
  position: absolute;
  bottom: 0;
}
.button1 {
  color: #f0f5f9;
  display: inline-block;
  position: relative;
  background: linear-gradient(to right, #1e2022 0%, #26a0da 51%, #1e2022 100%);
  transition: background ease 0.4s;
  width: 160px;
  height: 45px;
  line-height: 45px;
  cursor: pointer;
}
.button1:hover {
  transition: background ease 0.4s;
  background: linear-gradient(to right, #1e2022 0%, #26a0da 51%, #1e2022 100%);
}
.button1:hover:before {
  border-width: 1;
  border: 1px solid #5ea9e6;
  width: 100%;
  height: 100%;
  transition: width linear 0.4s, height linear 0.4s 0.4s, border-bottom linear 0s 0.8s, border-width linear 0s;
}
.button1:hover:after {
  width: 0%;
  transition: width linear 0.4s 0.8s;
}
.button1:before {
  width: 0;
  border-width: 0;
  border: 1px solid transparent;
  box-sizing: border-box;
  height: 0%;
  background: none;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  transition: width linear 0.3s 0.6s, height linear 0.3s 0.3s, border linear 0s 0.9s, border-bottom linear 0s 0.3s;
  z-index: 1;
}
.button1:after {
  position: absolute;
  border-bottom: 1px solid #3a4149;
  width: 100%;
  bottom: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  content: "";
  transition: width ease-out 0.3s;
  z-index: 2;
}


